:root {
  --time: 250ms;
  --cubic: cubic-bezier(.4, 0, .2, 1);
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

li {
  list-style: none;
}

#search-box {
  max-width: 400px;
  color: #2e3440;
  transition: var(--time) var(--cubic);
  margin-bottom: 16px;
  padding: 8px;
  font-size: max(1.2rem, min(2.5vw, 2.1rem));
  transition-property: box-shadow;
  box-shadow: .1rem .1rem .21rem .1rem rgba(67, 76, 94, .5);
}

#search-box:focus {
  box-shadow: .2rem .2rem .41rem .1rem rgba(129, 161, 193, .5);
}

.country_container_flags {
  width: max(2.3rem, min(3vw, 3rem));
  height: auto;
  margin: auto .5rem auto 0;
}

.country_container_flags--big {
  width: max(3rem, min(3vw, 4rem));
}

.country-search {
  padding: 30px;
}

.country-list, .country-info {
  max-width: max(200px, min(80%, 1000px));
  padding: 21px;
  box-shadow: .21rem .21rem .21rem .21rem rgba(67, 76, 94, .21);
}

.country_container_wraper, .country_list {
  align-items: center;
  display: flex;
  position: relative;
}

.country_list:not(:last-child):after, .country_container_wraper:after {
  content: "";
  width: 99%;
  background-color: rgba(67, 76, 94, .05);
  position: absolute;
}

.country_list:after {
  height: 1px;
  top: 100%;
  left: 0;
}

.country_container_wraper:after {
  height: 2px;
  top: calc(100% + .21rem);
  left: .5%;
}

.country-info, .country-list {
  transition: var(--time) var(--cubic);
  transition-property: opacity, visibility;
}

